import './App.css';
import { Route } from 'react-router-dom';
import Landing from './views/Landing/Landing';
import Home from './views/Home/Home';

function App() {
  return (
    <div className="App">
      <Route exact path = '/' component = {Landing}/>
      <Route exact path = '/Home' component = {Home}/>
    </div>
  );
}

export default App;
